import'./css/bootstrap.css'
import 'jquery'
import 'bootstrap'
import 'popper.js'

import React, { Component } from 'react';
import List from './List';
import Header from "./components/Header";
import Add from './Add'
import Edit from './Edit'
import Login from './Login'
import Cadastro from './Cadastro'
import Add_fluxo from './Add_fluxo'
import { BrowserRouter, Route } from "react-router-dom";
import { usuarios_index, registros_index, fluxo_index, registros_editar, registros_salvar } from "./Api";

class App extends Component {

  state = {
    login: false,
    nav_itens: [],
    titulo: 'Adicionar Fluxo',
    item_escolhido: { id: undefined, nome_fluxo: '' },
    registros: [],
    lista: []
  }

  login = (o) => usuarios_index(o).then(data => {
    // console.log(data)
    if (data.data) this.setState({ login: data.data });
    else { alert('erro no login') }
  });

  nav_fluxos = (o) => fluxo_index(o.obj.login).then(data => { this.setState({ nav_itens: data.data }) })

  fluxo_escolhido = (o) => registros_index(o.id).then(data => {
    this.setState({ item_escolhido: { id: o.id, nome_fluxo: o.nome_fluxo } })
    this.setState({ registros: data.data.reverse() })
    this.setState({ lista: data.data })
  })

  obterData = dado => dado.data.substr(0, 10).split("-")[1];

  escolhe_mes = (o) => {
    const a = this.state.lista.filter(dado_registro => {
      if (this.obterData(dado_registro) === o) return true
      if (o === 0) return true;
    })
    this.setState({ registros: a })
  }

  editar = (o) => registros_editar({ obj: o, login: this.state.login })
    // .then(data => console.log(data.data))

  built_list = () => {
    return this.state.nav_itens
      .filter(item => { if (item.id === this.state.item_escolhido.id) return true })
      .map(item => <Route exact key={item} render={(props => <List {...props}
        obj={item}
        escolhe_mes={this.escolhe_mes}
        registros={this.state.registros}
      />)} path="/" />)
  }

  salvar = (o) => registros_salvar({ a: o, b: this.state.login })
  // .then(data => console.log(data.data))


  render() {
 
    
    return (
      <BrowserRouter basename="/app/build/index.html">

        {!this.state.login &&
          <Route exact render={(props => <Login {...props}
            login={this.login} />)} path="/" />}


        {!this.state.login &&
          <Route exact render={props => <Cadastro {...props} />} path="/cadastro" />
        }



        {this.state.login &&
          <Route exact render={(props => <Header {...props}
            obj={this.state}
            nav_fluxos={this.nav_fluxos}
            fluxo_escolhido={this.fluxo_escolhido}
          />)} path="/" />}

        {this.state.login &&
          <Route exact path="/" render={props => <h1 style={{ textAlign: 'center' }}>{this.state.item_escolhido.nome_fluxo}</h1>} />}

        {this.state.login &&
          <Route render={props => <Add_fluxo obj={this.state} />} path="/add-fluxo" />}

        {this.state.login && this.built_list()}

        {this.state.login && <Route render={props => <Edit {...props}
          login={this.state.login}
          editar={this.editar}
          fluxo_escolhido={this.fluxo_escolhido}
          fluxos={this.state.nav_itens}
          item_escolhido={this.state.item_escolhido}
        />} path="/edit" />}

        {this.state.login && <Route exact render={props => <Add {...props}
          salvar={this.salvar} obj={this.state}
          item_escolhido={this.state.item_escolhido}
          fluxo_escolhido={this.fluxo_escolhido}
        />} path="/add" key="add" />}

      </BrowserRouter>
    );
  }
}

export default App;